@import url('https://fonts.googleapis.com/css2?family=Courgette&display=swap');


*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}

body{
background-color: white;
}

.screen1{
    /* background-color: rgba(0,0,0,0.02); */
    background-color: rgba(0, 0, 0, 0.02);
    width: 390px;
    /* margin: 20px auto; */
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 20px;
    padding-bottom: 50px;
    border-radius: 30px;
    box-shadow: 0 2px 5px black;
    border-left: 5px solid black;
    border-right: 5px solid black;
    font-family: 'Courgette',
    cursive;
    

}

.titleName{
    text-align: center;
    padding: 10px;
    text-shadow: 0 2px 5px white;
}

.current{
    padding: 10px;
}


.headcontainer{
    background-color: #4ffeef;
    text-align: center;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    /* padding-bottom: 10px; */
    border-top: 5px solid black;
    
}

.box-inner{
    display: flex;
    align-items: center;
    width: 340px;
    margin: 0 auto;
    padding-top: 10px;
    border-radius: 10px;
    padding-bottom: 10px;
    justify-content: center;
    /* background-color: white; */
    background-color: rgba(0, 0, 0, 0.06);
    box-shadow: 0 2px 5px grey;
    
}

.inner-div{
    
    display: flex;
    /* text-align: center; */
    align-items: center;
    justify-content: center;
    height: 35px;
    width: 35px;
    background-color: rgb(31, 31, 31);
    margin: 5px;
    border-radius: 10px;
    color: white;
    border: none;
    outline: none;
    /* border: 1px solid #FEF14E; */

}
.middel{
    color: black;
    background-color: #7D4EFE;
    border: none;
    outline: none;
    box-shadow: 0 2px 5px black;
    height: 40px;
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    margin: 3px;
    color: rgb(253, 251, 251);
}

.displayListdiv{
    display: flex;
    padding: 20px;
    width: 340px;
    justify-content: center;
    margin: auto;
    min-height: 300px;
    border-radius: 10px;
    margin-top: 10px;
    height: 300px;
    overflow:scroll;
    overflow-x:hidden;
    overflow-y: auto;
   
}


::-webkit-scrollbar {
    width: 3px;

}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: black;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: grey
}

ul li{
    /* background-color: hotpink; */
    /* background-image: linear-gradient(to right, #FEE94E,white); */
    margin: 10px;
    margin-right: 10px;
    padding: 8px;
    list-style: none;
    font-size: 14px;
    width: 330px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    z-index: -1;
}

.listdelbtn{
    float: right;
    border-radius: 50%;
    outline: none;
    background-color:rgba(0, 0, 0, 0.822);
    border: none;
    color: white;
    padding: 3px;
    position: relative;
    bottom: 4px;

}

input[type="checkbox"]{
    margin-right: 20px;
    margin-left: 5px;

}

input[type="text"]{
    padding: 10px;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.1);
    outline: none;
    border: 1px solid black;
    font-family: 'Courgette',
    cursive;
    width: 200px;
}

input[type="date"] {
    padding: 10px 10px;
    width: 150px;
    border-radius: 10px;
    margin-top: 5px;
    margin-left: 20px;
    background-color: rgba(0, 0, 0, 0.1);
    outline: none;
    border: 1px solid black;

}

textarea{
    width: 200px;
    min-height: 100px;
    padding: 10px;
    border-radius: 10px;
    background-color: rgba(0, 0, 0, 0.1);
    outline: none;
    border: 1px solid black;
    font-family: 'Courgette',
    cursive; 
}


.screen2{
    background-color: white;
    width: 280px;
    position: absolute;
    bottom: 50%;
    left: 50%;
    transform: translate(-53%,50%);
    padding: 30px;
    margin: 10px;
    border-radius: 20px;
    text-align: center;
    box-shadow: 0 2px 5px grey; 
    border-top: 5px solid black;
    border-bottom: 5px solid black;
    
}

.screen2 > div{
    margin: 5px;
    padding: 5px;
}

label{
    font-size: 15px;
}
.addtolistbtn{
    padding: 10px;
    position: fixed;
    bottom: 30%;
    left: 50%;
    transform: translate(-50%,100%);
    width: 200px;
    background-color: #865DF4;
    color: white;
    border-radius: 20px;
    outline: none;
    border: none;
    cursor: pointer;
    box-shadow: 0 2px 5px black;
    font-family: 'Courgette',
    cursive;
}

.addtolistbtn:hover{
    background-color: black;

}


.showscreen2btn{
    padding: 15px;
    width: 50px;
    margin: 10px auto;
    border-radius: 10px;
    outline: none;
    background-color: #7D4EFE;
    /* border: 1px solid black; */
    border: none;
    cursor: pointer;
    transition: 0.2s;
    box-shadow: 0 2px 5px black;
}

.showscreen2btn:hover{
    background-color: black;
    color: white;
    transform: translateY(-2px);
}

.btnwrap{
    text-align: center;
}

.nothingdiv{
    width: 350px;
    height: 50px;
    background-color: #5A5A5A;
    position: absolute;
    bottom: 30px;
    z-index: -1;
    border-radius: 10px;
    border-bottom: 5px solid black;
    border-left: 5px solid black;
    border-right: 5px solid black;

}
.duedate{
    display: flex;
    /* background-color: chartreuse; */
}
.duedate label{
    display: flex;
    align-items: center;
}


#blur.active{
    filter: blur(20px);
    pointer-events: none;
    user-select: none;

}
#blur2.active {
    filter: blur(20px);
    pointer-events: none;
    user-select: none;
}
#blur3.active {
    filter: blur(20px);
    pointer-events: none;
    user-select: none;
}

.unblur{
    display: inline-block;
}



.screen2wrapper{
    visibility: hidden;
    transition: 0.01s ease-in-out;
}

.screen2wrapper#popup.active {
    top: 40%;
    visibility: visible;
    transition: 0.01s ease-in-out;
}

.screen3{
        background-color: white;
        width: 300px;
        position: absolute;
        bottom: 50%;
        left: 50%;
        transform: translate(-53%, 50%);
        padding: 25px;
        margin: 10px;
        border-radius: 20px;
        box-shadow: 0 2px 5px grey;
        border-top: 5px solid black;
        border-bottom: 5px solid black;
        visibility: hidden; 
        transition: 0.01s ease-in-out;
        z-index: 10;

}

.screen3.active {
    visibility: visible;
    transition: 0.01s ease-in-out;

}

.screen3 .div1{
        text-align: center;

}

.screen3 .div2{

    padding-left: 5px;
    margin-top: 10px;
}

.screen3hidebtn{
    border-radius: 50%;
    outline: none;
    background-color:#5A5A5A;
    border: none;
    color: white;
    padding: 5px;
    position: relative;
    left: 90%;
    cursor: pointer;
    /* bottom: 165px;
    left: 220px; */

}

a{
    color: black;
    text-decoration: none;
}


.duedatepass{
    font-size: 12px;
    color: red;
    overflow: hidden;
    display: inline-block;
    position: relative;
    text-align: center;
    top:24px;
    left: 60px;
    /* background-color: yellow; */
    
}


